"use client";

import { Stock, useWatchlist, useToggleWatchlist } from "@/hooks/useStocks";
import { ArrowDown, ArrowUp, Minus, Star, Activity, Zap } from "lucide-react";
import { cn } from "@/lib/utils";
import { useState, useMemo } from "react";
import { useRouter } from "next/navigation";
import AutoFilter, { FilterState } from "./AutoFilter";
import { applyColumnFilters } from "@/utils/globalFilter";
import { getUniqueValues, getPctColor } from "@/utils/tableUtils";
import { StrengthMeter } from "@/components/ui/StrengthMeter";

interface StocksTableProps {
    stocks: Stock[];
    isLoading: boolean;
    simpleMode?: boolean;
    variant?: "card" | "plain";
    marketHeaders?: {
        current: string;
        p1: string;
        p2: string;
        p3: string;
    };
}

export default function StocksTable({ stocks, isLoading, simpleMode = false, variant = "card", marketHeaders }: StocksTableProps) {
    const router = useRouter();
    const [displayCount, setDisplayCount] = useState(50);

    // Default headers if not provided
    const headers = {
        current: marketHeaders?.current || "Today Change",
        p1: marketHeaders?.p1 || "Past Day 1 %",
        p2: marketHeaders?.p2 || "Past Day 2 %",
        p3: marketHeaders?.p3 || "Past Day 3 %"
    };

    // Client-side state
    const [columnFilters, setColumnFilters] = useState<Record<string, FilterState>>({});
    const [sortConfig, setSortConfig] = useState<{ key: string; direction: "asc" | "desc" }>({
        key: "current_change",
        direction: "desc"
    });

    const { data: watchlist } = useWatchlist();
    const { mutate: toggleWatchlist } = useToggleWatchlist();

    const isInWatchlist = (symbol: string) => {
        return watchlist?.some((s: any) => s.symbol === symbol);
    };

    const handleToggleWatchlist = (symbol: string) => {
        const action = isInWatchlist(symbol) ? "remove" : "add";
        toggleWatchlist({ symbol, action });
    };

    const handleLoadMore = () => {
        setDisplayCount(prev => prev + 50);
    };

    const handleRowClick = (symbol: string) => {
        router.push(`/stocks/${symbol}`);
    };

    const handleFilterChange = (key: string, filter: FilterState | null) => {
        setColumnFilters(prev => {
            const next = { ...prev };
            if (filter === null) {
                delete next[key];
            } else {
                next[key] = filter;
            }
            return next;
        });
    };

    const handleSort = (key: string, direction: "asc" | "desc") => {
        setSortConfig({ key, direction });
    };

    // Derived Data & Filtering
    const processedStocks = useMemo(() => {
        if (!stocks) return [];

        // 1. Enrich (Already done in parent page is assumed, or pass enriched data)
        // Note: With recent refactor, stocks passed here should be EnrichedStock[] effectively.
        // But the type signature says Stock[]. We assume properties exist or fallback.

        // 2. Apply Column Filters
        const filtered = applyColumnFilters(stocks, columnFilters);

        // 3. Sort
        return filtered.sort((a, b) => {
            const { key, direction } = sortConfig;

            // Special handling for Stability Rank (Absolute value close to 0)
            if (key === "history.avg_3day") {
                let valA = Math.abs(a.history?.avg_3day || 0);
                let valB = Math.abs(b.history?.avg_3day || 0);
                if (valA < valB) return direction === "asc" ? -1 : 1;
                if (valA > valB) return direction === "asc" ? 1 : -1;
                return 0;
            }

            const getValue = (obj: any, path: string) => {
                return path.split('.').reduce((o, i) => o?.[i], obj);
            };

            let valA = getValue(a, key);
            let valB = getValue(b, key);

            if (valA === undefined || valA === null) return 1;
            if (valB === undefined || valB === null) return -1;

            if (valA < valB) return direction === "asc" ? -1 : 1;
            if (valA > valB) return direction === "asc" ? 1 : -1;
            return 0;
        });

    }, [stocks, columnFilters, sortConfig]);

    const displayedStocks = processedStocks.slice(0, displayCount);

    const getOptions = (key: string) => {
        if (!stocks.length) return [];
        return getUniqueValues(stocks, key);
    };

    if (isLoading) {
        return (
            <div className="space-y-4">
                {[...Array(5)].map((_, i) => (
                    <div key={i} className="h-16 bg-gray-100 dark:bg-slate-800 rounded-lg animate-pulse" />
                ))}
            </div>
        );
    }

    return (
        <>
            <div className={cn("w-full overflow-x-auto min-h-[600px]",
                "rounded-2xl border border-gray-200 dark:border-slate-700 bg-white dark:bg-slate-800 shadow-sm dark:shadow-md"
            )}>
                <table className="min-w-full table-auto border-separate border-spacing-y-1 text-sm text-left relative">
                    <thead className="text-xs uppercase bg-gray-50 dark:bg-slate-800 text-gray-500 dark:text-gray-400 font-semibold tracking-wider">
                        <tr>
                            {!simpleMode && (
                                <th className="px-3 py-2 w-12 text-center sticky left-0 z-30 bg-gray-50 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700">Rank</th>
                            )}

                            {/* 2. Symbol (w-40) - Sticky Left */}
                            <th className={cn("px-3 py-2 w-40 sticky z-30 bg-gray-50 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700", simpleMode ? "left-0" : "left-12")}>
                                <div className="flex items-center justify-between gap-2">
                                    <span>Symbol</span>
                                    <AutoFilter
                                        columnKey="symbol"
                                        title="Symbol"
                                        type="text"
                                        options={getOptions("symbol")}
                                        onFilter={(f) => handleFilterChange("symbol", f)}
                                        currentFilter={columnFilters["symbol"] || null}
                                        onSort={(d) => handleSort("symbol", d)}
                                        sortDirection={sortConfig.key === "symbol" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>

                            {/* 3. Price (w-24) */}
                            <th className="px-3 py-2 w-24 text-right border-b border-gray-200 dark:border-slate-700">
                                <div className="flex items-center justify-end gap-2">
                                    <span>Price</span>
                                    <AutoFilter
                                        columnKey="current_price"
                                        title="Price"
                                        type="number"
                                        onFilter={(f) => handleFilterChange("current_price", f)}
                                        currentFilter={columnFilters["current_price"] || null}
                                        onSort={(d) => handleSort("current_price", d)}
                                        sortDirection={sortConfig.key === "current_price" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            {
                                !simpleMode && (
                                    <th className="px-3 py-2 w-28 text-center border-b border-border">
                                        <div className="flex items-center justify-center gap-2">
                                            <span>{headers.current}</span>
                                            <AutoFilter
                                                columnKey="current_change"
                                                title={headers.current}
                                                type="number"
                                                onFilter={(f) => handleFilterChange("current_change", f)}
                                                currentFilter={columnFilters["current_change"] || null}
                                                onSort={(d) => handleSort("current_change", d)}
                                                sortDirection={sortConfig.key === "current_change" ? sortConfig.direction : null}
                                            />
                                        </div>
                                    </th>
                                )
                            }
                            {
                                !simpleMode && (
                                    <>
                                        <th className="px-3 py-2 w-28 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>{headers.p1}</span>
                                                <AutoFilter
                                                    columnKey="history.p_day1"
                                                    title={headers.p1}
                                                    type="number"
                                                    onFilter={(f) => handleFilterChange("history.p_day1", f)}
                                                    currentFilter={columnFilters["history.p_day1"] || null}
                                                    onSort={(d) => handleSort("history.p_day1", d)}
                                                    sortDirection={sortConfig.key === "history.p_day1" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-28 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>{headers.p2}</span>
                                                <AutoFilter
                                                    columnKey="history.p_day2"
                                                    title={headers.p2}
                                                    type="number"
                                                    onFilter={(f) => handleFilterChange("history.p_day2", f)}
                                                    currentFilter={columnFilters["history.p_day2"] || null}
                                                    onSort={(d) => handleSort("history.p_day2", d)}
                                                    sortDirection={sortConfig.key === "history.p_day2" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-28 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>{headers.p3}</span>
                                                <AutoFilter
                                                    columnKey="history.p_day3"
                                                    title={headers.p3}
                                                    type="number"
                                                    onFilter={(f) => handleFilterChange("history.p_day3", f)}
                                                    currentFilter={columnFilters["history.p_day3"] || null}
                                                    onSort={(d) => handleSort("history.p_day3", d)}
                                                    sortDirection={sortConfig.key === "history.p_day3" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                    </>
                                )
                            }
                            <th className="px-3 py-2 w-28 text-center border-b border-border">
                                <div className="flex items-center justify-center gap-2">
                                    <span>3D Avg %</span>
                                    <AutoFilter
                                        columnKey="history.avg_3day"
                                        title="3D Avg %"
                                        type="number"
                                        onFilter={(f) => handleFilterChange("history.avg_3day", f)}
                                        currentFilter={columnFilters["history.avg_3day"] || null}
                                        onSort={(d) => handleSort("history.avg_3day", d)}
                                        sortDirection={sortConfig.key === "history.avg_3day" ? sortConfig.direction : null}
                                    />
                                </div>
                            </th>
                            {
                                !simpleMode && (
                                    <>
                                        <th className="px-3 py-2 w-40 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>Indicators</span>
                                                <AutoFilter
                                                    columnKey="derived.indicatorsList"
                                                    title="Indicators"
                                                    type="enum"
                                                    options={[
                                                        "MACD Above",
                                                        "MACD Below",
                                                        "MACD Neutral",
                                                        "RSI Over",
                                                        "RSI Under",
                                                        "RSI Neutral"
                                                    ]}
                                                    onFilter={(f) => handleFilterChange("derived.indicatorsList", f)}
                                                    currentFilter={columnFilters["derived.indicatorsList"] || null}
                                                    onSort={(d) => handleSort("derived.indicatorsList", d)}
                                                    sortDirection={sortConfig.key === "derived.indicatorsList" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-16 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>M</span>
                                                <AutoFilter
                                                    columnKey="derived.macdLabel"
                                                    title="M"
                                                    type="enum"
                                                    options={["Above", "Below", "Neutral"]}
                                                    onFilter={(f) => handleFilterChange("derived.macdLabel", f)}
                                                    currentFilter={columnFilters["derived.macdLabel"] || null}
                                                    onSort={(d) => handleSort("derived.macdLabel", d)}
                                                    sortDirection={sortConfig.key === "derived.macdLabel" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-16 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>R</span>
                                                <AutoFilter
                                                    columnKey="derived.rsiLabel"
                                                    title="R"
                                                    type="enum"
                                                    options={["Over", "Under", "Neutral"]}
                                                    onFilter={(f) => handleFilterChange("derived.rsiLabel", f)}
                                                    currentFilter={columnFilters["derived.rsiLabel"] || null}
                                                    onSort={(d) => handleSort("derived.rsiLabel", d)}
                                                    sortDirection={sortConfig.key === "derived.rsiLabel" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-40 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>Today Strength</span>
                                                <AutoFilter
                                                    columnKey="current_strength"
                                                    title="Today Strength"
                                                    type="enum"
                                                    options={["BUYERS", "SELLERS", "BALANCED"]}
                                                    onFilter={(f) => handleFilterChange("current_strength", f)}
                                                    currentFilter={columnFilters["current_strength"] || null}
                                                    onSort={(d) => handleSort("current_strength", d)}
                                                    sortDirection={sortConfig.key === "current_strength" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-40 text-center border-b border-border">
                                            <div className="flex items-center justify-center gap-2">
                                                <span>3D Avg Str</span>
                                                <AutoFilter
                                                    columnKey="avg_3day_strength"
                                                    title="3D Avg Str"
                                                    type="enum"
                                                    options={["BUYERS", "SELLERS", "BALANCED"]}
                                                    onFilter={(f) => handleFilterChange("avg_3day_strength", f)}
                                                    currentFilter={columnFilters["avg_3day_strength"] || null}
                                                    onSort={(d) => handleSort("avg_3day_strength", d)}
                                                    sortDirection={sortConfig.key === "avg_3day_strength" ? sortConfig.direction : null}
                                                />
                                            </div>
                                        </th>
                                        <th className="px-3 py-2 w-12 text-center border-b border-border">Action</th>
                                    </>
                                )
                            }
                        </tr>
                    </thead>

                    <tbody className="divide-y divide-border">
                        {displayedStocks.map((stock) => (
                            <tr
                                key={stock.symbol}
                                className="hover:bg-accent/50 transition-colors group cursor-pointer border-b border-border last:border-0"
                                onClick={() => handleRowClick(stock.symbol)}
                            >
                                {!simpleMode && <td className="px-3 py-2 font-medium text-muted-foreground text-center">#{stock.rank}</td>}
                                <td className="px-3 py-2 sticky left-0 z-10 bg-card group-hover:bg-accent/50 border-r border-border whitespace-nowrap">
                                    <div className="font-bold text-foreground">{stock.symbol}</div>
                                    <div className="text-[10px] text-muted-foreground">{stock.sector}</div>
                                </td>
                                <td className="px-3 py-2 font-mono text-foreground text-right whitespace-nowrap">â‚¹{stock.current_price.toFixed(2)}</td>
                                {!simpleMode && (
                                    <td className={cn("px-3 py-2 font-medium text-center whitespace-nowrap", getPctColor(stock.current_change))}>
                                        <div className="flex items-center justify-center gap-1">
                                            {stock.current_change > 0 ? <ArrowUp className="w-3 h-3" /> : stock.current_change < 0 ? <ArrowDown className="w-3 h-3" /> : <Minus className="w-3 h-3" />}
                                            {stock.current_change?.toFixed(2) || "0.00"}%
                                        </div>
                                    </td>
                                )}
                                {!simpleMode && (
                                    <>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.p_day1))}>
                                            {stock.history.p_day1?.toFixed(2) || "-"}%
                                        </td>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.p_day2))}>
                                            {stock.history.p_day2?.toFixed(2) || "-"}%
                                        </td>
                                        <td className={cn("px-3 py-2 text-xs text-center whitespace-nowrap", getPctColor(stock.history.p_day3))}>
                                            {stock.history.p_day3?.toFixed(2) || "-"}%
                                        </td>
                                    </>
                                )}
                                <td className={cn("px-3 py-2 font-medium text-center whitespace-nowrap", getPctColor(stock.history.avg_3day))}>
                                    <div className="flex items-center justify-center gap-1">
                                        {stock.history.avg_3day > 0 ? <ArrowUp className="w-3 h-3" /> : stock.history.avg_3day < 0 ? <ArrowDown className="w-3 h-3" /> : <Minus className="w-3 h-3" />}
                                        {stock.history.avg_3day?.toFixed(2) || "0.00"}%
                                    </div>
                                </td>
                                {!simpleMode && (
                                    <>
                                        <td className="px-3 py-2 whitespace-nowrap text-[10px] text-center text-muted-foreground">
                                            <div className="flex flex-col items-center gap-0.5">
                                                <span className={cn(stock.derived.macdLabel === "Above" ? "text-green-600 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-600 dark:text-red-400" : "text-muted-foreground")}>
                                                    MACD: {stock.derived.macdLabel}
                                                </span>
                                                <span className={cn(stock.derived.rsiLabel === "Under" ? "text-green-600 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-600 dark:text-red-400" : "text-muted-foreground")}>
                                                    RSI: {stock.derived.rsiLabel}
                                                </span>
                                            </div>
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap text-center">
                                            <Activity className={cn("w-4 h-4 mx-auto", stock.derived.macdLabel === "Above" ? "text-green-500 dark:text-green-400" : stock.derived.macdLabel === "Below" ? "text-red-500 dark:text-red-400" : "text-muted-foreground")} />
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap text-center">
                                            <Zap className={cn("w-4 h-4 mx-auto", stock.derived.rsiLabel === "Under" ? "text-green-500 dark:text-green-400" : stock.derived.rsiLabel === "Over" ? "text-red-500 dark:text-red-400" : "text-blue-400 dark:text-blue-500")} />
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap">
                                            <StrengthMeter label={stock.current_strength} />
                                        </td>
                                        <td className="px-3 py-2 whitespace-nowrap">
                                            <StrengthMeter label={stock.avg_3day_strength} />
                                        </td>
                                        <td className="px-3 py-2 text-center whitespace-nowrap">
                                            <button
                                                className="p-1.5 hover:bg-accent rounded-full transition-colors text-muted-foreground hover:text-yellow-500 dark:hover:text-yellow-400"
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    handleToggleWatchlist(stock.symbol);
                                                }}
                                            >
                                                <Star className={cn("w-4 h-4", isInWatchlist(stock.symbol) ? "fill-yellow-500 text-yellow-500 dark:text-yellow-400 dark:fill-yellow-400" : "")} />
                                            </button>
                                        </td>
                                    </>
                                )}
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {!simpleMode && processedStocks.length > displayCount && (
                <div className="mt-4 flex justify-center">
                    <button
                        onClick={handleLoadMore}
                        className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-full text-sm font-medium transition-colors shadow-sm"
                    >
                        Load More Stocks
                    </button>
                </div>
            )
            }
        </>
    );
}
